/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react'
import { ToastContainer } from 'react-toastify'
import {
  Card,
  CardHeader,
  CardBody,
  Row,
  Col,
  Form,
  Input,
  FormGroup,
  Label,
  Button
} from 'reactstrap'
import { connect } from 'react-redux'
import { Monitor, Smartphone, Tablet } from 'react-feather'
import '../../../assets/scss/pages/authentication.scss'
import ComingSoon from '../misc/ComingSoon'
import { Plans, Features, WhyUs } from './MenuOptions'
import Banner from '../../mydashboard/Banner'
const LandingPage = (props) => {
  const { plan, why, feature } = props
  const [email, setEmail] = useState('')
  const [q, setQ] = useState('')
  const handlingemail = (e) => {
    e.preventDefault()
    setEmail(e.target.value)
  }
  const handlingq = (e) => {
    e.preventDefault()
    setQ(e.target.value)
  }

  useEffect(() => {
    plan &&
      document.getElementById('plan').scrollIntoView({ behavior: 'smooth' })
    why &&
      document.getElementById('whyUs').scrollIntoView({ behavior: 'smooth' })
    feature &&
      document.getElementById('feature').scrollIntoView({ behavior: 'smooth' })
  }, [plan, why, feature])

  return (
    <div>
      <Row className="d-flex align-items-center justify-content-center m-0">
        <div
          style={{
            marginTop: '4.8rem'
          }}
          className="w-100 h-100"
        >
          <ToastContainer />
          <div
            style={{
              width: '100%',
              marginLeft: window.screen.width <= 500 ? '0px' : '-2px'
            }}
          >
            <Banner />
          </div>
          <div
            className="w-100 d-block"
            style={{
              minHeight: '0px',
              marginTop: plan || why || feature ? '20px' : '0px',
              overflow: 'hidden'
            }}
          >
            {/* {why && <WhyUs />} */}
            {/* {plan && <Plans />} */}
            {/* {feature && <Features />} */}
            <WhyUs />
            <Features />
            <Plans />
          </div>
          <Row className="w-100 m-0">
            <Col md="7" className={plan || why || feature ? 'mt-0' : 'mt-2'}>
              <Card className="mb-0">
                <CardHeader className="justify-content-center">
                  <h3>We are here to help your dependents</h3>
                </CardHeader>
                <CardBody className="text-center">
                  <div className="divider">
                    <a href="/#/#/register" className="divider-text">
                      <h5>Register</h5>
                    </a>
                  </div>
                  <Col className="text-center d-flex justify-content-center flex-wrap">
                    <h4>You can access your account from any platform</h4>
                  </Col>
                  <Row className="mt-4">
                    <Col className="text-center justify-content-center">
                      <Smartphone size="35" />
                      <h5>Smartphones</h5>
                    </Col>
                    <Col className="text-center justify-content-center">
                      <Monitor size="35" />
                      <h5>PCs</h5>
                    </Col>
                    <Col className="text-center justify-content-center">
                      <Tablet size="35" />
                      <h5>Tabs</h5>
                    </Col>
                  </Row>
                  <div className="divider">
                    <span className="divider-text">
                      <h5>Query/Feedback</h5>
                    </span>
                  </div>
                  <Form>
                    <FormGroup className="form-label-group mt-2">
                      <Input
                        placeholder="Email"
                        className="text-center mb-1"
                        onChange={handlingemail}
                      />
                      <Label>Email</Label>

                      <Input
                        placeholder="Query/Suggestion"
                        className="text-center"
                        onChange={handlingq}
                      />
                      <Label>Query/Suggestion</Label>
                    </FormGroup>
                  </Form>
                  <Button.Ripple
                    block
                    color={email && q ? 'warning' : 'primary'}
                    className="btn-block"
                  >
                    Submit
                  </Button.Ripple>
                </CardBody>
              </Card>
            </Col>
            <Col
              md="5"
              className={plan || why || feature ? 'mt-0' : 'mt-2'}
              style={{
                marginBottom: window.screen.width <= 500 ? '100px' : '0px'
              }}
            >
              <ComingSoon />
            </Col>
          </Row>{' '}
        </div>
      </Row>
    </div>
  )
}
const mapStateToProps = (state) => {
  const user = state.auth
  const { customizer } = state?.customizer
  return {
    messages: customizer?.language?.Dashboard,
    sender: customizer?.language?.Sender,
    user: user,
    plan: user?.login?.plan,
    why: user?.login?.why,
    feature: user?.login?.feature
  }
}
export default connect(mapStateToProps)(LandingPage)
